<template>
    <div id="login">
        <h1>注册</h1>

        账号<input type="text" name="username" v-model="username"><br>
        密码<input type="password" name="password" v-model="password">
        <button @click="clkRegister">clkRegister</button>
    </div>
</template>
<script>
export default {
    name:"Login",
    data(){
        return{
            username:'',
            password:''
        }
    },
    methods:{
        clkRegister(){
            var a= this
            this.$axios({
                url:'/register/userRegister?username='+a.username+'&password='+a.password,
                method:'get'
            })
            .then(res=>{
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'success'
                    });
                this.$router.push({ name: "Login" });
                
            })
            .catch(error=>{
                console.log(error);
                console.log('请求失败')})   
        }
    }
}
</script>
