<template>
    <div id="personalPage">
        <div>
            账号:{{username}}
        </div>
        <div>
            密码:{{password}}
        <div><input type="text" v-model="editpassword"></div>
        <button @click="editpwd">修改密码</button>
        </div>
        <div id="personalheadimg"><img :src="headimg" alt=""></div>
        <div>
            昵称:{{nickname}}
        <div><input type="text" v-model="editnickname"></div>
        <button @click="editniknam">修改</button>
        </div>
        <div>
            生日:{{birth}}
        <div><input type="text" v-model="editbirth"></div>
        <button @click="editbth">修改</button>
        </div>
    </div>
</template>
<script>
export default {
    name:'Personal',
    data(){
        return {
            editpassword:'',
            editnickname:'',
            editbirth:''
        }
    },
    methods:{
        editpwd(){
            console.log('wcwq');
        },
        editniknam(){
            var that = this;
            this.$axios({
                url:'/personal/editnickname?nickname='+that.editnickname,
                method:'get'
            })
            
            .then((res,err)=>{
                 console.log(res.data,"gaga");
                sessionStorage.setItem('nickname',res.data.data.nickname)
               
                if(err){
                    this.$message.error('修改失败');
                }
                else{
                    that.$store.state.nickname=sessionStorage.getItem('nickname')
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'success'
                    });
                }
            })
            
        },
        editbth(){
            var that = this;
            this.$axios({
                url:'/personal/editbirth?birth='+that.editnickname,
                method:'get'
            })
            
            .then((res,err)=>{
                 console.log(res.data,"gaga");
                sessionStorage.setItem('birth',res.data.data.birth)
               
                if(err){
                    this.$message.error('修改失败');
                }
                else{
                    that.$store.state.nickname=sessionStorage.getItem('birth')
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'success'
                    });
                }
            })
        }

    },
    computed:{
        username(){
            return this.$store.state.username
        },
        password(){
            return this.$store.state.password
        },
        headimg(){
            return this.$store.state.headimg
        },
        nickname(){
            return this.$store.state.nickname
        },
        birth(){
            return this.$store.state.birth
        }
    }
}
</script>
<style>
    #personalheadimg{
    width: 100px;
    height: 100px;
    }
    #personalheadimg img{
        width: 100%;
        height: 100%;
    }
</style>